@font-face {
  font-family: 'cwTeX-q-font';
  src: url('./assets/font/cwTeXQMing-Medium.ttf');
}

body {
  margin: 0;
  padding: 0;
  font-family: 'cwTeX-q-font', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  //font-size: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

html {
  font-size: 10px;
}

@media screen and (max-width: 350px) {
  html {
    font-size: 9.1px;
  }
}